html {
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu";
  background-image: linear-gradient(-225deg, #e21c34 0%, #500b28 100%);
  min-height: 100%;
  padding-bottom: 5rem;
}
.border-top {
  padding: 8px 0;
  border-width: 10px 0 0;
  border-top-style: solid;
  border-image: linear-gradient(139deg, #000000, #680505, #ff0000)4;
}

h1,
h2 {
  color: white;
  padding: 3rem 0;
  text-align: center;
  font-weight: bold;

  font-family: "Ubuntu", sans-serif;
  letter-spacing: 1px;
}
.lista-citas .media {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 3rem;
}

.lista-citas .media:last-of-type {
  border-bottom: 0;
}
.lista-citas .media-body p {
  margin: 0;
}
.lista-citas .media-body button {
  margin-top: 1rem !important;
}
.lista-citas .media-body span {
  font-weight: bold;
}

label {
  color: white;
}

input[type="date"],
input[type="time"] {
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

.cita {
  padding: 2rem;
  background: white;
  border-bottom: 1px solid #e1e1e1;
  color: black;
}

.cita p {
  font-weight: bold;
  margin-bottom: 0.8rem;
}
.cita p span {
  font-weight: normal;
}
.cita:first-of-type {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.cita:last-of-type {
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.button.eliminar,
.button.eliminar:hover {
  background-color: #c10059;
  color: white;
  margin-top: 2rem;
}

.alerta-error {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 245, 245);
  padding: 1rem;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
  font-family: "Staatliches", cursive;
  border-radius: 10px;
}

.button-add {
  background-image: linear-gradient(
    to right,
    #190a05 0%,
    #870000 51%,
    #190a05 100%
  );
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  font-size: 20px;
  border-radius: 10px;
  display: block;
}

.button-add:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
